                    <img class="img-responsive center-block" src="@/assets/img/about_img.png">
                </div>
                <div class="col-xs-12 col-sm-12 col-md-6">
                    <h3>广州聚亿鑫贸易有限公司</h3>
                    <p class=".text-justify">聚亿鑫是一家以技术贸易发展导向的科技公司</p>
                    <p class=".text-justify">公司主营：聚亿鑫电线电缆、聚亿鑫香水洗衣粉、聚亿鑫天然香水皂液、聚亿鑫天然香水皂粉、聚亿鑫天然婴幼儿皂粉、聚亿鑫天然植物皂粉、聚亿鑫天然植物皂液、聚亿鑫去油香橙洗洁精等 洗手液十大品牌，洗洁精十大品牌，中国驰名品牌，最具市场竞争力品牌，中国优秀民营企业，高新技术企业，国内日化龙头企业大型现代化专业洗衣粉、洗衣液生产企业，行业最具竞争实力的知名企业，中国洗护类化妆品优秀品牌，是一家技术力量雄厚，工艺设备先进，集洗涤用品生产、销售及加工的企业！</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { WOW } from 'wowjs';
export default {
    name: 'CompanyIntroduction',
    data(){
        return{

        }
    },
    mounted(){
        var wow = new WOW();
        wow.init();
    }
}
</script>
<style scoped>
.banner{
    color: #fff;
    font-size: 30px;
    height: 150px;
    line-height: 150px;
    background-image: url('../assets/img/banner1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: scroll;
    background-position: center center;
}
.row{
    margin-right: 0;
    margin-left: 0;
}
.CompanyIntroduction-container{
    padding: 100px 0;
    color: #808080;
    transition: all ease 0.5s;
}
.CompanyIntroduction-container>div>p{
    font-size: 14px;
    line-height: 2.5rem;
}
@media screen and (max-width: 997px){
    .CompanyIntroduction-container{
    padding: 10px 0;
    color: #808080;
}
}
</style>




